.portfolio{
    padding: 50px;
}

.page_heading .line1{

    height: 5px;
width: 50px;
background: var(--skin-color);
border-radius: 10px;
margin-top: 5px;
}
.page_heading .line2{
    height: 5px;
width: 25px;
background: var(--skin-color);
border-radius: 10px;
margin-top: 5px;

}
.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap : 5px
}

.portfolio_container .portfolio_box{
    height: 300px;
    padding : 25px;
    background: #f2f2fc;
    overflow: hidden;
}
.portfolio_container .portfolio_box:hover{
    background: transparent !important;
}
@media screen and (max-width : 1200px) {
    .portfolio_container{
        grid-template-columns: repeat(2, 1fr);
    }
}

.portfolio_container .portfolio_box .portfolio_image{
    height: 100%;
    width: 100%;
}

.portfolio_container .portfolio_box .portfolio_image img{
    height: 100%;
    width: 100%;
}

.portfolio_container .portfolio_box .read_more{
    color: var(--skin-color);

}

@media screen and (max-width : 600px) {
    .portfolio_container{
        grid-template-columns: repeat(1, 1fr);
    }
}
